class Dropdown {

	static init = () => {
		// bind dropdown events
		setTimeout(() => {
			let dropdowns = document.querySelectorAll(".dropdown > button")
			if (dropdowns) {
				dropdowns.forEach(dropdown => {
					Dropdown.bind(dropdown)
				})
			}
		}, 100)
	}

	static bind = (dropdown) => {
		if (!dropdown.parentNode.classList.contains("split-button")) {
			dropdown.classList.add("icon-right")
		}
		dropdown.addEventListener("click", Dropdown.toggle)
		dropdown.addEventListener("blur", Dropdown.hide)
	}

	static toggle = (event) => {
		var dropdown = event.target
		var ul = dropdown.parentNode.querySelector("ul")
		ul.classList.toggle("active")
	}

	static show = (event) => {
		var dropdown = event.target
		dropdown.focus()
		var ul = dropdown.parentNode.querySelector("ul")
		ul.classList.add("active")
	}

	static hide = (event) => {
		var dropdown = event.target
		dropdown.blur()
		var ul = dropdown.parentNode.querySelector("ul")
		ul.classList.remove("active")
	}
}

export default Dropdown